import React from 'react';
import classes from './ScheduledAppointments.module.css';
import EditIcon from '../../Images/edit.png';
import DeleteIcon from '../../Images/delete.png';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import axios from 'axios';
import moment from 'moment';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

const url = process.env.REACT_APP_URL;

class ScheduledAppointmentList extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            noAppointments: "No Scheduled Appointments",
            scheduledAppointmentData: [],
            isModelVisiable: false,
            appointmentId: 0
        };
    }
    auth = {
        username: 'superman',
        password: 'Admin123'
    };

    componentDidMount() {

        let today = new Date();
        let date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + (today.getDate() - 1);
        let time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
        let dateTime = date + 'T' + time + '.000Z';


        axios.get(`${url}/phrappointment/all?forDate=${dateTime}`, {
            auth: this.auth
        })
            .then(responce => {
                console.log("Responce", responce)
                this.setState({ scheduledAppointmentData: responce.data })
            })
            .catch(error => {
                console.log("Error", error)
            })

    }
    render() {

        const editHandler = (event) => {
            var scheduledAppointment = this.state.scheduledAppointmentData.filter(function (item) {
                return item.uuid === event.target.id;
            });
            this.props.onClickEdit(scheduledAppointment);
        }

        const cancelHandler = (event) => {
            if (this.state.isModelVisiable) {
                this.setState({
                    isModelVisiable: false,
                    appointmentId: event.target.id
                })
            } else {
                this.setState({
                    isModelVisiable: true,
                    appointmentId: event.target.id
                })
            }
        }

        const closeHandler = () => {
            console.log("adfsaas")
            if (this.state.isModelVisiable) {

                this.setState({
                    isModelVisiable: false
                })
            } else {

                this.setState({
                    isModelVisiable: true
                })
            }
        }

        const cancelAppointmentHandler = (event) => {
            let data = {
                toStatus: "Cancelled"
            }
            axios.post(`${url}/appointment/${this.state.appointmentId}/changeStatus`, data, { auth: this.auth })
                .then(responce => {
                    console.log("Responffdgdgdce", responce)
                    window.location.reload(false);
                })
                .catch(error => {
                    console.log("Error", error)
                })

        }

        const LightTooltip = withStyles((theme) => ({
            tooltip: {
                backgroundColor: theme.palette.common.white,
                color: 'rgba(0, 0, 0, 0.87)',
                boxShadow: theme.shadows[2],
                fontSize: 11,
            },
        }))(Tooltip);


        let scheduledAppointmentList = this.state.scheduledAppointmentData.length > 0
            && this.state.scheduledAppointmentData.map((item, i) => {

                let theDate = moment(item.startDateTime).format("MMM DD, YYYY h:mm A").split(" ");
                console.log("Sring", theDate)
                return (
                    <List className={classes.root, classes.listItem}>
                        <ListItem alignItems="flex-start">
                            <ListItemText
                                primary={
                                    <React.Fragment>
                                        <Grid container md={12} sm={12} xs={12}>

                                            <Grid container md={2} sm={1} xs={0}></Grid>

                                            <Grid container md={8} sm={10} xs={12} style={{ border: "2px solid #ccc", padding: "2%" }}>

                                                <Grid container md={12} sm={12} xs={12}>
                                                    <Grid container md={8} sm={9} xs={9}>
                                                        <label className={classes.labelClass}>{theDate[0]} {theDate[1]}{theDate[2]}&nbsp;&nbsp;&nbsp;{theDate[3]} {theDate[4]}</label>
                                                    </Grid>

                                                    <Grid md={1} sm={0} xs={0}></Grid>

                                                    <Grid md={3} sm={3} xs={3} >
                                                        <Grid container md={12} sm={12} xs={12} style={{ textAlign: "right" }}>
                                                            <Grid md={8} sm={6} xs={6}>
                                                                <LightTooltip title="Edit Appointment" placement="bottom-start">
                                                                    <img id={item.uuid} onClick={editHandler} className={classes.iconSize} src={EditIcon} alt="" />
                                                                </LightTooltip>
                                                            </Grid>
                                                            <Grid md={4} sm={6} xs={6}>
                                                                <LightTooltip title="Cancel Appointment" placement="bottom-start">
                                                                    <img id={item.uuid} onClick={cancelHandler} className={classes.deleteIcon} src={DeleteIcon} alt="" />
                                                                </LightTooltip>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>

                                                </Grid>

                                                <Grid container md={12} sm={12} xs={12}>
                                                    <label className={classes.labelClass}>{item.patient.name}</label>
                                                </Grid>
                                                <Grid container md={12} sm={12} xs={12}>
                                                    <label className={classes.labelClass}>{item.service.name}</label>
                                                </Grid>
                                                <Grid container md={12} sm={12} xs={12}>
                                                    {item.provider !== null ? <label className={classes.labelClass}>{item.provider.name}</label> : null}
                                                </Grid>
                                            </Grid>
                                            <Grid container md={2} sm={1} xs={0}></Grid>
                                        </Grid>
                                    </React.Fragment>
                                }
                            />
                        </ListItem>
                    </List>
                )
            }, this);

        return (
            <Grid md={12} sm={12} xs={12}>

                <div id="myModal" className={classes.modal} style={this.state.isModelVisiable ? { display: "block" } : { display: "none" }}>

                    <div className={classes.modalContent}>
                        <div className={classes.close} >
                            <span style={{ paddingRight: "50px" }} onClick={closeHandler} style={this.state.isModelVisiable ? { display: "block" } : { display: "none" }}>&times;</span>
                        </div>
                        <div className={classes.modalBody}>
                            <label className={classes.labelPopup}  >Are you sure you want to cancel your appointment ?</label>
                            <div style={{ marginTop: "10px" }}>
                                <button className={classes.buttonYes} onClick={cancelAppointmentHandler} >Yes</button>
                                <button className={classes.buttonNo} onClick={closeHandler} >No</button>
                            </div>

                        </div>
                    </div>
                </div>

                {this.state.scheduledAppointmentData.length > 0 ? scheduledAppointmentList :
                    <div className={classes.noScheduledContent}>
                        {this.state.noAppointments}
                    </div>}

            </Grid>
        );
    };
};
export default ScheduledAppointmentList;